












































import Mixin from '@/mixins';
import LessonsList from '@/components/dataviews/LessonsList.vue';
import DataViewer from '@/components/DataViewer.vue';
import Component, { mixins } from 'vue-class-component';
import { Watch, Prop } from 'vue-property-decorator';
@Component({
  components: { LessonsList, DataViewer }
})
export default class TimetableComponent extends mixins(Mixin) {
  active = 0;
  // @Prop({ default: 0 }) w!: number;
  cweek = 0; //this.w
  timetable = {};
  selectedLesson = false;

  @Watch('cweek')
  onCWeekChanged() {
    this.timetable = {};
    this.obtain('timetable', this.cweek).then(v => (this.timetable = v));
  }

  mounted() {
    this.onCWeekChanged();
  }
  get week() {
    return this.getWeek(this.cweek);
  }
}
