













































import { utc2date } from '@/helpers';
import Mixin from '@/mixins';
import Component, { mixins } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
@Component
export default class EvaluationsTable extends mixins(Mixin) {
  @Prop(Array) readonly evaluations!: any[];
  @Prop(Array) readonly classAvarages!: any[];

  keys: (string | number)[] = [];
  get groupedEvaluations() {
    return this.group(this.evaluations, 'subject');
  }
  get groupedClassAvarages() {
    return this.group(this.classAvarages, 'subject');
  }
  get evaluationsMatrix() {
    const ret = { ...this.groupedEvaluations },
      keys: any[] = [];
    for (const [subject, evaluations] of Object.entries(ret)) {
      const grouped = {};
      // @ts-ignore
      for (const evaluation of evaluations) {
        // @ts-ignore
        let key;
        if (evaluation.type == 'MidYear') {
          key = `0${utc2date(evaluation.date).getMonth() + 1}`.slice(-2);
        } else {
          key = {
            EndYear: 'Évvégi',
            HalfYear: 'Félévi'
          }[evaluation.type];
        }

        if (!(key in grouped)) {
          grouped[key] = [];
        }
        grouped[key].push(evaluation);
        keys.push(key);
      }
      ret[subject] = grouped;
    }
    this.keys = keys
      .filter((v, i, a) => {
        return a.indexOf(v) == i;
      })
      .sort();
    return ret;
  }
  getClasses(evaluation) {
    return {
      'green--text': evaluation.mode == 'Beszámoló',
      'blue--text': evaluation.mode == 'Gyakorlati feladat',
      'red--text': evaluation.weight != '100%',
      'gray--text': evaluation.mode == 'Órai munka',
      'teal--text-lighten-2': evaluation.mode == 'Házi dolgozat',
      'font-weight-bold': evaluation.weight == '200%'
    };
  }
  getAvarage(subject) {
    const evaluations = this.groupedEvaluations[subject];
    let sum = 0,
      n = 0;
    for (const evaluation of evaluations) {
      if (evaluation.isAtlagbaBeleszamit && evaluation.form == 'Mark') {
        let weight = evaluation.weight.slice(0, -1) * 1;
        sum += weight * evaluation.numberValue;
        n += weight;
      }
    }
    return round(sum / n);
  }
  getClassAvarage(subject) {
    return this.groupedClassAvarages[subject]
      ? this.groupedClassAvarages[subject][0].value
      : null;
  }
  getDifference(subject) {
    if (!this.getClassAvarage(subject)) return '-';
    return round(this.getAvarage(subject) - this.getClassAvarage(subject));
  }
  getDifferenceColor(subject) {
    const diff = this.getDifference(subject);
    return diff == 0 ? '' : diff > 0 ? 'green--text' : 'red--text';
  }
}
function round(n) {
  return Math.round(100 * n) / 100;
}
