import Vue from 'vue';
import { apiMapper } from '@/store';
import Component from 'vue-class-component';
import { toldyLink, formatDate, day, formatTime, utc2date } from './helpers';
import { JustificationState, Note, Evaluation, Item } from './api-types';

@Component({
  computed: apiMapper.mapState({
    state: state => state
  })
})
export default class Mixin extends Vue {
  group(arr: any[], key: string) {
    const result = {};
    arr.forEach(item => {
      if (!result[item[key]]) {
        result[item[key]] = [];
      }
      result[item[key]].push(item);
    });
    return result;
  }

  getAbsenceColor(justificationState: JustificationState) {
    return {
      Justified: 'green',
      BeJustified: 'orange',
      UnJustified: 'red'
    }[justificationState];
  }
  getKey(item) {
    return `${item.id}-${item.date}`;
  }
  noteValues({ content, type, title, date, teacher, creatingTime }: Note) {
    return {
      Tartalom: content,
      Típus: type,
      Cím: title,
      Dátum: this.formatDate(date),
      Tanár: this.toldyLink(teacher),
      'Naplózás dátuma': `${this.formatDate(creatingTime)}, ${this.formatTime(
        creatingTime
      )}`
    };
  }
  evalValues({
    value,
    weight,
    theme,
    subject,
    mode,
    typeName,
    date,
    teacher,
    creatingTime
  }: Evaluation) {
    return {
      Jegy: `${value} (${weight})`,
      Téma: theme || '-',
      Tantárgy: subject,
      Mód: mode,
      Típus: typeName,
      Dátum: this.formatDate(date),
      Tanár: this.toldyLink(teacher),
      'Naplózás dátuma': `${this.formatDate(creatingTime)}, ${this.formatTime(
        creatingTime
      )}`
    };
  }
  lessonValues({
    date,
    startTime,
    endTime,
    teacher,
    deputyTeacher,
    subject,
    theme,
    homework,
    classRoom,
    count
  }) {
    return {
      Időpont: `${count}. óra, ${this.formatDate(date)}; ${this.formatTime(
        startTime,
        false
      )} - ${this.formatTime(endTime, false)}`,
      Tantárgy: subject,
      Téma: theme,
      Tanár: this.toldyLink(
        deputyTeacher ? `Helyettesítő: ${deputyTeacher}` : teacher
      ),
      Terem: classRoom,
      Házi: homework
    };
  }
  absenceValues({
    typeName,
    subject,
    date,
    justificationTypeName,
    teacher,
    creatingTime
  }: Item) {
    return {
      Típus: typeName,
      Tantárgy: subject,
      Dátum: this.formatDate(date),
      'Igazolás típusa': justificationTypeName,
      Tanár: this.toldyLink(teacher),
      'Naplózás dátuma': `${this.formatDate(creatingTime)}, ${this.formatTime(
        creatingTime
      )}`
    };
  }
  eventValues({ content, date, endDate }) {
    return {
      Tartalom: content.replace(/\n/gm, '<br/>'),
      Dátum: this.formatDate(date),
      'Utoljára látható': this.formatDate(endDate)
    };
  }
  getWeek(week: number) {
    function _format(d: Date): string {
      return `${d.getFullYear()}-${`0${d.getMonth() + 1}`.slice(
        -2
      )}-${`0${d.getDate() + 1}`.slice(-2)}`;
    }
    let now = new Date();
    now.setHours(0, 0, 0, 0);
    now.setDate(now.getDate() + week * 7);
    let monday = new Date(now);
    monday.setDate(monday.getDate() - monday.getDay() - 1);
    let sunday = new Date(now);
    sunday.setDate(sunday.getDate() - sunday.getDay() + 5);
    return { from: _format(monday), to: _format(sunday) };
  }
  get mobile() {
    return innerWidth < 600;
  }

  async obtain(what: string, w = 0) {
    // @ts-ignore
    let resource = this.state[what],
      week,
      range;
    if (what == 'timetable') {
      week = this.getWeek(w);
      range = `${week.from}-${week.to}`;
      // @ts-ignore
      resource = this.state.timetable[range];
    }
    if (
      resource &&
      !resource.loading &&
      resource.data &&
      (what == 'general' ? !!resource.data.id : true)
    )
      return resource.data;

    return this.$store.dispatch(
      `api/pull${what.charAt(0).toUpperCase() + what.slice(1)}`,
      week
    );
  }

  debounce(func: Function, wait: number, immediate: boolean) {
    var timeout;
    return () => {
      var context = this,
        args = arguments;
      var later = function() {
        timeout = null;
        if (!immediate) func.apply(context, args);
      };
      var callNow = immediate && !timeout;
      clearTimeout(timeout);
      timeout = setTimeout(later, wait);
      if (callNow) func.apply(context, args);
    };
  }
  toldyLink = toldyLink;
  formatDate = formatDate;
  formatTime = formatTime;
  utc2date = utc2date;
  day = day;
}
