






































import Mixin from '@/mixins';
import Component, { mixins } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
@Component
export default class EvaluationList extends mixins(Mixin) {
  @Prop() readonly evaluations!: any[];
  @Prop(Array) readonly classAvarages!: any[];

  sortBy = 'date';
  get sorted() {
    switch (this.sortBy) {
      case 'date':
        return {
          '': this.evaluations.sort((a, b) => b.date - a.date)
        };
      case 'creatingTime':
        return {
          '': this.evaluations.sort((a, b) => b.creatingTime - a.creatingTime)
        };
      case 'value':
        return this.group(this.evaluations, 'value');
      case 'subject':
      default:
        return this.group(this.evaluations, 'subject');
    }
  }
  sortFlags = [
    {
      id: 'date',
      text: 'Dátum',
      icon: 'mdi-timeline-text'
    },
    {
      id: 'creatingTime',
      text: 'Beírás',
      icon: 'mdi-timeline-plus'
    },
    {
      id: 'subject',
      text: 'Tárgy',
      icon: 'mdi-format-list-bulleted-square'
    },
    {
      id: 'value',
      text: 'Értékelés',
      icon: 'mdi-format-list-bulleted-type'
    }
  ];
}
