










import { Evaluation } from '@/api-types';
import Mixin from '@/mixins';
import { apiMapper } from '@/store';
import Component, { mixins } from 'vue-class-component';
@Component({
  computed: apiMapper.mapState({
    absences: state => state.general.data.absences,
    evaluations: state => state.general.data.evaluations.reverse()
  })
})
export default class Statistics extends mixins(Mixin) {
  evaluations!: Evaluation[];
  selectedSubject = false;
  get values() {
    // function gcd(a, b) {
    //   return !b ? a : gcd(b, a % b);
    // }

    // let min = this.evaluations[0].date,
    //     evals = this.evaluations.map(e => {
    //       e.date = (e.date - min) / 3600
    //       return e
    //     }),
    //     divider = evals[0].date

    // evals.forEach(function(n) {
    //     divider = gcd(divider, n).date
    // })
    //let labels = evals.map(e => e.date * 3600)
    return {
      value: this.evaluations.map(e => {
        return e.numberValue;
      }),
      labels: this.evaluations.map(e => {
        //console.log(e.date);
        let d = new Date(e.date * 1000);
        return `${`0${d.getMonth() + 1}`.slice(-2)}. ${`0${d.getDate()}`.slice(
          -2
        )}`;
      })
    };
  }
}
