








































import Mixin from '@/mixins';
import Component, { mixins } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

const EVENT_VISIBILITY_CHANGE = 'visibilitychange';
@Component
export default class NextLessonCard extends mixins(Mixin) {
  @Prop() readonly timetable: any;
  time = new Date();
  timeoutId: undefined | number = undefined;
  requestId: number = 0;

  mounted() {
    document.addEventListener(
      EVENT_VISIBILITY_CHANGE,
      this.handleVisibilityChange
    );

    if (document.visibilityState === 'visible') {
      this.continue();
    }
  }

  get mode() {
    let timetableKey = new Date().setHours(1, 0, 0, 0) / 1000;
    let lessons = this.timetable[timetableKey] || [];
    if (
      lessons.length > 0 &&
      lessons.slice(-1)[0].startTime * 1000 > +new Date()
    ) {
      return 'today';
    }
    timetableKey += 24 * 60 * 60;
    lessons = this.timetable[timetableKey] || [];
    if (lessons.length > 0) {
      return 'tomorrow';
    }
    return 'hidden';
  }

  get timetableKey() {
    return (
      this.time.setHours(1, 0, 0, 0) / 1000 +
      (this.mode == 'tomorrow' ? 24 * 60 * 60 : 0)
    );
  }

  get lessons() {
    return this.timetable[this.timetableKey] || [];
  }

  get nextLesson() {
    if (this.mode == 'today') {
      for (let l of this.lessons) {
        if (l.startTime > this.time && l.endTime > this.time) {
          return l;
        }
      }
    }
    return null;
  }

  get timeTillNextLesson() {
    if (this.mode == 'today') {
      let ht = '',
        offset = this.time.getUTCMinutes() - this.nextLesson.startTime;
      if (offset > 60) {
        ht = `${Math.floor(offset / 60)} óra és `;
      }

      return `${ht}${offset % 60} perc múlva`;
    }
    return null;
  }

  get changes() {
    if (this.mode != 'tomorrow') return [];
    const substitued = this.lessons.filter(l => !!l.deputyTeacher);
    const missed = this.lessons.filter(l => l.state == 'Missed');
    let ret: any[] = [];
    if (substitued.length) {
      ret.push({
        color: '',
        text: ` amiből `
      });
      ret.push({
        color: 'green--text',
        text: `${substitued.length}-n helyettesítés lesz`
      });
    }
    if (missed.length) {
      if (substitued.length) {
        ret.push({
          color: '',
          text: ` és `
        });
      } else {
        ret.push({
          color: '',
          text: ` amiből `
        });
      }
      ret.push({
        color: 'green--text',
        text: `${missed.length} elmarad`
      });
    }
    return ret;
  }

  continue() {
    if (window.requestAnimationFrame) {
      let start;
      const step = timestamp => {
        if (!start) {
          start = timestamp;
        }

        if (timestamp - start < 1000) {
          this.requestId = requestAnimationFrame(step);
        } else {
          this.progress();
        }
      };

      this.requestId = requestAnimationFrame(step);
    } else {
      this.timeoutId = setTimeout(() => {
        this.progress();
      }, 1000);
    }
  }

  /**
   * Pauses the countdown.
   * @private
   */
  pause() {
    if (window.requestAnimationFrame) {
      cancelAnimationFrame(this.requestId);
    } else {
      clearTimeout(this.timeoutId);
    }
  }

  /**
   * Progresses to countdown.
   * @private
   * @emits Countdown#progress
   */
  progress() {
    this.time = new Date();
    this.continue();
  }

  handleVisibilityChange() {
    switch (document.visibilityState) {
      case 'visible':
        this.progress();
        break;

      case 'hidden':
        this.pause();
        break;

      default:
    }
  }

  beforeDestroy() {
    document.removeEventListener(
      EVENT_VISIBILITY_CHANGE,
      this.handleVisibilityChange
    );
    this.pause();
  }
}
